:root {
  --sc-bg: #111318;
  --sc-bg-sidebar: #161920;
  --sc-border: rgba(255, 255, 255, 0.08);
  --sc-text-main: #e6edf3;
  --sc-text-dim: #8b949e;
  --sc-accent: #58a6ff;
  --sc-success: #238636;
  --sc-error: #da3633;
  --sc-warning: #9a6700;
  --sc-font-mono: 'Fira Code', 'JetBrains Mono', Consolas, monospace;
}

.sc-dsl-editor {
  position: fixed;
  inset: 0;
  z-index: 999999;
  display: none;
  font-family: system-ui, -apple-system, sans-serif;
}

.sc-dsl-editor--open {
  display: block;
}

.sc-dsl-editor__backdrop {
  position: absolute;
  inset: 0;
  background: rgba(15, 17, 19, 0.75);
  backdrop-filter: blur(4px);
}

.sc-dsl-editor__panel {
  position: absolute;
  top: 20px;
  bottom: 20px;
  right: 20px;
  width: min(900px, 95vw);
  max-height: calc(100vh - 40px);
  background: var(--sc-bg);
  color: var(--sc-text-main);
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  border: 1px solid var(--sc-border);
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.6);
  overflow: hidden;
  animation: sc-slide-in 0.2s ease-out;
}

@keyframes sc-slide-in {
  from {
    transform: translateX(20px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;
  }
}

/* HEADER */
.sc-dsl-editor__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 16px;
  border-bottom: 1px solid var(--sc-border);
  background: linear-gradient(90deg, #1a1d25, #14161b);
}

.sc-dsl-editor__title-main {
  font-size: 14px;
  font-weight: 600;
}

.sc-dsl-editor__title-sub {
  font-size: 11px;
  opacity: 0.5;
  font-family: var(--sc-font-mono);
  display: block;
}

.sc-dsl-editor__header-actions {
  display: flex;
  gap: 8px;
  align-items: center;
}

.sc-dsl-editor__btn-icon {
  background: none;
  border: none;
  color: #fff;
  opacity: 0.6;
  cursor: pointer;
  transition: 0.2s;
  font-size: 16px;
  padding: 4px;
}

.sc-dsl-editor__btn-icon:hover {
  opacity: 1;
  color: var(--sc-accent);
}

.sc-dsl-editor__close {
  background: none;
  border: none;
  color: #fff;
  font-size: 20px;
  cursor: pointer;
  opacity: 0.6;
  transition: 0.2s;
  padding: 4px;
}

.sc-dsl-editor__close:hover {
  opacity: 1;
}

/* BODY & LAYOUT */
.sc-dsl-editor__body {
  flex: 1;
  display: flex;
  overflow: hidden;
}

.sc-dsl-editor__main-area {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-width: 0;
}

.sc-dsl-editor__editor-container {
  flex: 1;
  background: #0d1117;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.sc-dsl-editor__editor {
  flex: 1;
  overflow: auto;
  font-size: 13px;
}

/* SIDEBAR (ŚCIĄGAWKA) - POPRAWIONA CZYTELNOŚĆ */
.sc-dsl-editor__sidebar {
  width: 280px;
  background: var(--sc-bg-sidebar);
  border-left: 1px solid var(--sc-border);
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.sc-dsl-editor__sidebar-header {
  padding: 12px 16px;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  color: var(--sc-text-dim);
  border-bottom: 1px solid var(--sc-border);
  letter-spacing: 1px;
}

.sc-dsl-editor__sidebar-content {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
}

.sc-cs-section {
  margin-bottom: 24px;
}

.sc-cs-title {
  font-size: 10px;
  color: var(--sc-accent);
  font-weight: 600;
  margin-bottom: 8px;
  font-family: var(--sc-font-mono);
  padding-left: 4px;
}

.sc-cs-item {
  display: flex;
  flex-direction: column;
  padding: 6px 8px;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 0.1s;
  margin-bottom: 2px;
}

.sc-cs-item:hover {
  background: rgba(255, 255, 255, 0.04);
  border-color: rgba(255, 255, 255, 0.05);
}

.sc-cs-item.is-used {
  opacity: 0.4;
  pointer-events: none;
}

/* Klucz i opis w sidebarze */
.sc-cs-item {
  font-family: var(--sc-font-mono);
  font-size: 13px;
  color: var(--sc-text-main);
}

.sc-cs-item span {
  font-family: system-ui, sans-serif;
  font-size: 11px;
  color: var(--sc-text-dim);
  margin-top: 3px;
}

/* STATUS BAR */
.sc-dsl-editor__status {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 12px;
  font-family: var(--sc-font-mono);
  color: var(--sc-text-dim);
  background: transparent;
  padding: 0;
  border: none;
  max-width: 60%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sc-dsl-editor__status-icon {
  font-size: 14px;
  line-height: 1;
  transition: color 0.2s;
}

.sc-dsl-editor__status--ok .sc-dsl-editor__status-icon {
  color: var(--sc-success);
}

.sc-dsl-editor__status--ok .sc-dsl-editor__status-text {
  color: var(--sc-text-dim);
}

.sc-dsl-editor__status--warning .sc-dsl-editor__status-icon {
  color: var(--sc-warning);
}

.sc-dsl-editor__status--warning .sc-dsl-editor__status-text {
  color: var(--sc-warning);
}

.sc-dsl-editor__status--error .sc-dsl-editor__status-icon {
  color: var(--sc-error);
}

.sc-dsl-editor__status--error .sc-dsl-editor__status-text {
  color: var(--sc-error);
}

/* FOOTER */
.sc-dsl-editor__footer {
  padding: 12px 16px;
  background: var(--sc-bg);
  border-top: 1px solid var(--sc-border);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
}

.sc-dsl-editor__actions {
  display: flex;
  gap: 10px;
  margin-left: auto;
}



.sc-dsl-editor__btn {
  font-size: 12px;
  font-weight: 500;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: 0.2s;
}

.sc-dsl-editor__btn--ghost {
  background: transparent;
  border-color: rgba(255, 255, 255, 0.2);
  color: var(--sc-text-main);
}

.sc-dsl-editor__btn--ghost:hover {
  border-color: #fff;
}

.sc-dsl-editor__apply {
  background-color: var(--sc-accent) !important;
  color: #fff !important;
}

/* SCROLLBARS */
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

::-webkit-scrollbar-track {
  background: var(--sc-bg);
}

::-webkit-scrollbar-thumb {
  background: #30363d;
  border: 2px solid var(--sc-bg);
  border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
  background: #58a6ff;
}

/* CODEMIRROR LINT STYLES */
.cm-lintRange-error {
  background: rgba(248, 81, 73, 0.2);
  border-bottom: 2px solid var(--sc-error);
}

.cm-lintRange-warning {
  background: rgba(227, 179, 65, 0.2);
  border-bottom: 2px solid var(--sc-warning);
}

.cm-tooltip.cm-tooltip-lint {
  background: #1f232b !important;
  border: 1px solid #30363d !important;
  color: var(--sc-text-main) !important;
  font-size: 11px;
  padding: 8px !important;
}

/* RESPONSIVE & ANIMATIONS */
@keyframes sc-shake {

  0%,
  100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-5px);
  }

  75% {
    transform: translateX(5px);
  }
}

.sc-shake .sc-dsl-editor__panel {
  animation: sc-shake 0.3s ease-in-out;
}

/* Ghost Mode */
.sc-dsl-editor--ghost .sc-dsl-editor__panel {
  opacity: 0.1;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.sc-dsl-editor--ghost .sc-dsl-editor__header {
  pointer-events: all;
  /* Keep header clickable to exit ghost */
  opacity: 0.8 !important;
}

/* Minimized Mode */
.sc-dsl-editor--minimized .sc-dsl-editor__panel {
  bottom: 0;
  right: 20px;
  top: auto;
  height: 40px;
  width: 300px;
  overflow: hidden;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.sc-dsl-editor--minimized .sc-dsl-editor__body,
.sc-dsl-editor--minimized .sc-dsl-editor__footer {
  display: none;
}

.sc-dsl-editor--minimized .sc-dsl-editor__header {
  height: 100%;
  padding: 0 12px;
}

@media (max-width: 800px) {
  .sc-dsl-editor__body {
    flex-direction: column;
  }

  .sc-dsl-editor__sidebar {
    width: 100%;
    height: 160px;
    border-top: 1px solid var(--sc-border);
    border-left: none;
  }

  .sc-dsl-editor__main-area {
    border-right: none;
  }
}

/* --- RESPONSYWNOŚĆ (MOBILE) --- */
@media (max-width: 600px) {
  .sc-dsl-editor__footer {
    flex-direction: column-reverse;
    align-items: stretch;
    gap: 12px;
  }

  .sc-dsl-editor__actions {
    margin-left: 0;
    justify-content: flex-end;
  }

  .sc-dsl-editor__status {
    max-width: 100%;
    justify-content: center;
    background: rgba(255, 255, 255, 0.03);
    padding: 6px;
    border-radius: 4px;
  }
}